.main_div{
  
    width: 100%;
}

.center_div{
    width: 80%;
}

.plusBtn{
    background-color: teal;
    color: white;
    border-radius: 25px;
    border: 0px;
    height: 50px;
    width: 54px;
    font-size: 30px;
}

ol {
    margin-top: 30px;
  

  }
  
  
  
  ol li {
    padding-left: 20px;
    text-align: left;
    font-size: 20px;
    font-weight: 500;
    min-height: 40px;
    display: flex;
   align-items: center;
   background-color: teal;
  color:white;
  margin-top: 1px;
  text-transform: capitalize;
   list-style: none;
  
  }

  .plusBtn:hover {

    color: #16a085 !important;

    background: white !important;
  
  
  }

  .todo_li{
      display: grid;
      grid-template-columns: 15% 85%;
      text-align: center;
      justify-content: center;
  }

  .del{
      padding: 2px;
      height: 100%;
      width: 100%;
      text-align: center;
      margin: auto;
      margin-top: 10px;
      background-color: white;
      box-shadow: 0px 0px 5px;
      border-radius: 50px;
      color: teal;

  }

  .del:hover{
      color: red;
  }